import React, { useState } from "react";
import { Link } from "react-router-dom";
import {
  Icon,
  List,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Collapse,
} from "@mui/material";
import ExpandLess from "@mui/icons-material/ExpandLess";
import ExpandMore from "@mui/icons-material/ExpandMore";

export default function SidebarSubmenu({ menu }) {
  const [open, setOpen] = useState(false);

  const handleClick = () => {
    setOpen(!open);
  };
  return (
    <React.Fragment>
      <ListItemButton onClick={handleClick}>
        <ListItemIcon>
          <Icon className="icon">{menu.icon}</Icon>
        </ListItemIcon>
        <ListItemText className="title" primary={menu.title} />
        {open ? <ExpandLess /> : <ExpandMore />}
      </ListItemButton>
      <Collapse in={open} timeout="auto" unmountOnExit>
        <List component="div" disablePadding>
          {menu.children.map((submenu, index) => {
            return (
              <ListItemButton key={index + "submenu"} sx={{ pl: 4 }}>
                <ListItemIcon>
                  <Icon className="icon">{submenu.icon}</Icon>
                </ListItemIcon>
                <Link to={submenu.path}>
                  <ListItemText className="title" primary={submenu.title} />
                </Link>
              </ListItemButton>
            );
          })}
        </List>
      </Collapse>
    </React.Fragment>
  );
}
