import React, { useState } from "react";
import { Button, TextField, Box, Typography, Container } from "@mui/material";
import axios from "axios";
import { useNavigate } from "react-router-dom";

export default function Login() {
  const [formValue, setFormValue] = useState({ email: "", password: "" });
  const navigate = useNavigate();
  const handleSubmit = (event) => {
    event.preventDefault();
    axios
      .post(process.env.REACT_APP_BACKEND_URL + "api/users/login", formValue)
      .then((response) => {
        sessionStorage.setItem("_id", response.data.user._id);
        sessionStorage.setItem("role", response.data.user.role);
        sessionStorage.setItem("user", response.data.user.email);
        navigate("/users");
      })
      .catch((error) => console.error(error));
  };

  return (
    <div className="login__page">
      <Container
        className="login__page__containe"
        component="main"
        maxWidth="xs"
      >
        <Box
          sx={{
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Typography component="h3" variant="h6">Sign In</Typography>
          <Box component="form" onSubmit={handleSubmit} sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              placeholder="Email Address"
              autoFocus
              type="email"
              onChange={(e) =>
                setFormValue({ ...formValue, email: e.target.value })
              }
              autoComplete="off"
              value={formValue.email}
            />
            <TextField
              margin="normal"
              required
              fullWidth
              placeholder="Password"
              type="password"
              onChange={(e) =>
                setFormValue({ ...formValue, password: e.target.value })
              }
              autoComplete="off"
              value={formValue.password}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              Sign In
            </Button>
          </Box>
        </Box>
      </Container>
    </div>
  );
}
