import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { DataGrid } from "@mui/x-data-grid";
import axios from "axios";
import { toast } from "react-toastify";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import { userColumns } from "../constant/constant";
import { v4 as uuidv4 } from "uuid";
import { Button, TextField, Box, Typography, Modal, InputLabel, Select, MenuItem, FormControl } from "@mui/material";

export default function Users() {
  const [users, setUsers] = useState([]);
  const initialFormValues = {
    userName: "",
    email: "",
    password: "",
    confirmPassword:"",
    mobile: "",
    location: "",
    role: "",
  };
  const [formValue, setFormValue] = useState(initialFormValues);
  const [open, setOpen] = useState(false);
  const [type, setType] = useState();
  const handleOpen = (type, data = {}) => {
    setType(type);
    setOpen(true);
    if (type === "View") setFormValue(data);
    if (type === "Add") setFormValue(initialFormValues);
  };
  const handleClose = () => {
    setFormValue(initialFormValues);
    setOpen(false);
  };
  useEffect(() => {
    getUsers();
  }, []);
  const getUsers = () => {
    axios
      .get(process.env.REACT_APP_BACKEND_URL + "api/users/list")
      .then((response) => setUsers(response.data.usersResult))
      .catch((error) => console.error(error));
  };

  const getRowId = (row) => {
    return row.id || uuidv4();
  };

  const actionColumn = [
    {
      field: "action",
      headerName: "Action",
      width: 350,
      renderCell: (params) => {
        return (
          <div className="cellAction">
            <div
              className="viewButton"
              onClick={() => handleOpen("View", params.row)}
            >
              View
            </div>
          </div>
        );
      },
    },
  ];

  const handleSubmit = () => {
    // Check if formValue contains any empty values
    if (
      Object.values(formValue).some((value) => value === "" || value === null)
    ) {
      toast.warning("Please fill in all fields");
      return; // Exit the function if there are empty values
    }
    axios
      .post(process.env.REACT_APP_BACKEND_URL + "api/users/signup", formValue)
      .then((response) => {
        toast.success(response.data.message);
        handleClose();
        getUsers();
      })
      .catch((error) => {
        toast.warning(error.response.data.message);
      });
  };

  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 500,
    height: 540,
    bgcolor: "background.paper",
    borderRadius: 2,
    boxShadow: 24,
    p: 4,
    overflow: "auto",
  };

  return (
    <div className="main">
      <Sidebar />
      <div className="main-container">
        <Navbar />
        <div className="datatable">
          <div className="datatableTitle">
            All Users
            <Link onClick={() => handleOpen("Add")} className="link">
              Add New
            </Link>
          </div>
          <DataGrid
            className="datagrid"
            rows={users}
            getRowId={getRowId}
            columns={userColumns.concat(actionColumn)}
            pageSize={9}
            rowsPerPageOptions={[9]}
          />
        </div>
      </div>
      <Modal open={open} onClose={handleClose}>
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="p" component="h3">
            {type} User
            <hr />
          </Typography>
          <Box component="form" sx={{ mt: 1 }}>
            {Object.keys(formValue).map((field, ind) => {
              return type === "Add" || type === "Edit" ? (
                field !== "role" ? (
                  <TextField
                    key={field}
                    margin="normal"
                    required
                    fullWidth
                    placeholder={
                      field.charAt(0).toUpperCase() +
                      field.slice(1).toLowerCase()
                    }
                    onChange={(e) =>
                      setFormValue({ ...formValue, [field]: e.target.value })
                    }
                    value={formValue[field]}
                    type={
                      field === "password"
                        ? "password"
                        : "text" && field === "confirmPassword"
                        ? "confirmPassword"
                        : "text"
                    }
                  />
                ) : (
                  <FormControl
                    sx={{ marginTop: 1, minWidth: "100%" }}
                    size="small"
                    key={field}
                  >
                    <InputLabel id="demo-simple-select-label">Role</InputLabel>
                    <Select
                      labelId="demo-simple-select-label"
                      id="demo-simple-select"
                      label="Role"
                      value={formValue[field]}
                      onChange={(e) =>
                        setFormValue({
                          ...formValue,
                          [field]: e.target.value,
                        })
                      }
                    >
                      <MenuItem value="operational">Operational Admin</MenuItem>
                      <MenuItem value="agent">Agent</MenuItem>
                      <MenuItem value="builder">Builder</MenuItem>
                      <MenuItem value="lead">Lead Admin</MenuItem>
                      <MenuItem value="internal">Internal Admin</MenuItem>
                    </Select>
                  </FormControl>
                )
              ) : (
                <div className="inventory-data" key={ind}>
                  <>
                    <span>{field}:</span>
                    <span> {formValue[field]}</span>
                  </>
                </div>
              );
            })}
            {type === "Add" && (
              <Button
                variant="contained"
                sx={{ mt: 3, mb: 2, ml: "auto" }}
                onClick={handleSubmit}
              >
                Submit
              </Button>
            )}
            <Button
              variant="outlined"
              sx={{ mt: 3, mb: 2, ml: 2 }}
              onClick={handleClose}
            >
              Cancel
            </Button>
          </Box>
        </Box>
      </Modal>
    </div>
  );
}
