import React, { useEffect, useState } from "react";
import axios from "axios";
import { v4 as uuidv4 } from "uuid";
import { Link } from "react-router-dom";
import { DataGrid } from "@mui/x-data-grid";
import Navbar from "./Navbar";
import Sidebar from "./Sidebar";
import { toast } from "react-toastify";
import { inventoryColumns } from "../constant/constant";
import {
  Button,
  TextField,
  Box,
  Typography,
  Modal,
} from "@mui/material";
import UploadFileIcon from "@mui/icons-material/UploadFile";

export default function Property() {
  const [inventoryLists, setInventoryLists] = useState([]);
  const [open, setOpen] = useState(false);
  const [type, setType] = useState("");
  const [selectedFiles, setSelectedFiles] = useState([]);
  const role = sessionStorage.getItem("role");
  const initialFormValues = {
    propertyName: "",
    location: "",
    sqft: "",
    sqftRate: "",
    bhk: "",
    address: "",
    amenties: "",
    propertyStatus: "",
    propertyFace: "",
    file: "",
  };
  const [formValue, setFormValue] = useState(initialFormValues);
  useEffect(() => {
    getInventoryList();
  }, []);
  const getInventoryList = () => {
    axios
      .get(process.env.REACT_APP_BACKEND_URL + "api/inventory/list")
      .then((response) => setInventoryLists(response.data.invResult))
      .catch((error) => console.error(error));
  };
  const getRowId = (row) => {
    return row.id || uuidv4();
  };
  const handleView = (id) => {
    handleOpen("view");
    axios
      .get(process.env.REACT_APP_BACKEND_URL + "api/inventory/details/" + id)
      .then((response) => setFormValue(response.data.invResult))
      .catch((error) => console.error(error));
  };
  const handleApprove = (id) => {
    axios
      .put(process.env.REACT_APP_BACKEND_URL + "api/inventory/approval", {
        id: id,
        status: "approved",
      })
      .then((response) => {
        console.log(response)
        if (response.status === 200) {
          toast.success(response.data.message);
          getInventoryList();
        }
      })
      .catch((error) => console.error(error));
  }
  const actionColumn = [
    {
      field: "action",
      headerName: "Action",
      width: 350,
      renderCell: (params) => {
        return (
          <div className="cellAction">
            {role === "oprational" && params.row.status === "pending" && (
              <div
                className="viewButton"
                onClick={() => handleApprove(params.row._id)}
              >
                Approve
              </div>
            )}
            <div
              className="viewButton"
              onClick={() => handleView(params.row._id)}
            >
              View
            </div>
            <div className="editButton">Edit</div>
          </div>
        );
      },
    },
  ];
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 500,
    bgcolor: "background.paper",
    borderRadius: 2,
    boxShadow: 24,
    p: 4,
  };
  const handleOpen = (type) => {
    setOpen(true);
    setType(type);
  };
  const handleClose = () => {
    setFormValue(initialFormValues);
    setOpen(false);
    setType("");
  };
  const handleFileChange = (event) => {
    const files = event.target.files;
    setSelectedFiles([...selectedFiles, ...files]);
  };
  const handleSubmit = () => {
    console.log("selected image ", selectedFiles);
    const formData = new FormData();

    formData.append("files", selectedFiles);
    formData.append("propertyName", formValue.propertyName);
    formData.append("location", formValue.location);
    formData.append("sqft", formValue.sqft);
    formData.append("bhk", formValue.bhk);
    formData.append("address", formValue.address);
    formData.append("amenties", formValue.amenties);
    formData.append("propertyStatus", formValue.propertyStatus);
    formData.append("propertyFace", formValue.propertyFace);
    axios
      .post(
        process.env.REACT_APP_BACKEND_URL + "api/inventory/createproperty",
        formData
      )
      .then((response) => {
        if (response.status === 201) {
          toast.success("Property details added successfully");
          getInventoryList();
          setSelectedFiles([]);
          handleClose();
        }
      })
      .catch((error) => console.error(error));
  };
  return (
    <div className="main">
      <Sidebar />
      <div className="main-container">
        <Navbar />
        <div className="datatable">
          <div className="datatableTitle">
            Property List
            <Link
              onClick={() => {
                handleOpen("add");
              }}
              className="link"
            >
              Add New Property
            </Link>
          </div>
          <DataGrid
            className="datagrid"
            rows={inventoryLists}
            getRowId={getRowId}
            columns={inventoryColumns.concat(actionColumn)}
            pageSize={9}
            rowsPerPageOptions={[9]}
          />
        </div>
      </div>
      <Modal open={open} onClose={handleClose}>
        <Box sx={style}>
          <Typography id="modal-modal-title" variant="p" component="h3">
            {type.charAt(0).toUpperCase() + type.slice(1).toLowerCase()}{" "}
            Property
            <hr />
          </Typography>
          <Box component="form" sx={{ mt: 1 }}>
            {Object.keys(formValue).map((field, ind) => {
              return type === "add" || type === "edit" ? (
                field !== "file" ? (
                  <TextField
                    key={field}
                    margin="normal"
                    required
                    fullWidth
                    placeholder={
                      field.charAt(0).toUpperCase() +
                      field.slice(1).toLowerCase()
                    }
                    onChange={(e) =>
                      setFormValue({ ...formValue, [field]: e.target.value })
                    }
                    value={formValue[field]}
                  />
                ) : (
                  <React.Fragment key={field}>
                    <Button
                      component="label"
                      variant="outlined"
                      startIcon={<UploadFileIcon />}
                      sx={{ mt: 2 }}
                    >
                      Upload Images
                      <input
                        type="file"
                        hidden
                        multiple
                        onChange={handleFileChange}
                      />
                    </Button>
                  </React.Fragment>
                )
              ) : (
                <div className="inventory-data" key={ind}>
                  <>
                    <span>{field}:</span>
                    <span> {formValue[field]}</span>
                  </>
                </div>
              );
            })}
            <br />
            {type === "add" && (
              <Button
                variant="contained"
                sx={{ mt: 3, mb: 2, ml: "auto" }}
                onClick={handleSubmit}
              >
                Submit
              </Button>
            )}
            <Button
              variant="outlined"
              sx={{ mt: 3, mb: 2, ml: 2 }}
              onClick={handleClose}
            >
              Cancel
            </Button>
          </Box>
        </Box>
      </Modal>
    </div>
  );
}
